import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  show = false;
  constructor() { }

  ngOnInit(): void {
  }

  openSideBar() {
    this.show = true;
  }

  closeSideBar() {
    this.show = false;
  }

}
